import { createRouter, createWebHistory } from 'vue-router'

import Root from './../root.vue'
import Homepage from '@/views/Pages/Homepage/Homepage.vue'
import NameFirstPage from '@/views/Pages/NameFirstPage/NameFirstPage.vue'
import PictureFirstPage from '@/views/Pages/PictureFirstPage/PictureFirstPage.vue'
import i18n from '@/i18n.js'


const routes = [
  {
    path: '/',
    redirect: `/${i18n.global.locale}/home`
  },
  {
    path: '/:lang',
    component: Root,
    children: [
    {
      path: 'home',
      name: 'Home_Debug',
      component: Homepage
    },
    {
      path: 'nameFirst',
      name: 'NameFirstPage',
      component: NameFirstPage
    },
    {
      path: 'pictureFirst',
      name: 'PictureFirstPage',
      component: PictureFirstPage
    },
  ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
