<template>
<div v-if="data != null" id="pictureFirstParent">
    <product-head 
    :isDefaultGuy="(rightArticle != selectedArticle) ? isHidden : true" 
    :productCategory="data.products[data.rightProductID].productCategory"
    class="productHead"/>

    <div id="productsParent">
        <div id="currentProductImageParent">
            <img id="currentProductImage" :src="data.products[data.rightProductID].imageURL" draggable="false"/>
        </div>
        <div id="productNameListParent"> 
            <selectable-proudct 
            class ="productElement"
            v-for="product in data.products"
            :key="product.article"
            :isSelected="selectedArticle === product.article" 
            :productUrl="product.productURL"
            :productName="product.productName"
            :productCategory="product.productCategory"
            :isHidden="isHidden"
            :isRight="rightArticle === product.article"
            :isWrong="wrongArticle === product.article"
            @click="productItemClicked(product)"
            />
        </div>
    </div>

    <div id="WrongOrRight">
        <span :class="WrongOrRightStyle">
            {{WrongOrRightText}}
        </span>
    </div>


    <div id="buttons">
        <button 
        class="blueButton btn" 
        :disabled="isApplyButtonDisabled"
        @click="applyChoice"> 
            {{ $t('nameFirst.CheckIt')}}
        </button>

        <button 
        class="whiteButton btn" 
        :disabled='isNextRoundButtonDisabled'
        @click="nextRound"> 
            {{ $t('nameFirst.Next')}}
        </button>
    </div>

    <div id="statisticParent">
        <div>
            <span class="fontBlue">{{ $t('nameFirst.CurrentScore')}}</span>
            <span>{{currentScore}}</span>
        </div>
        <div>
            <span class="fontBlue">{{ $t('nameFirst.HighScore')}}</span>
            <span>{{highScore}}</span>
        </div>
    </div>


</div>
</template>

<script>
import productHead from './components/Header.vue';
import selectableProudct from './components/SelectableProduct.vue';
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex';
import i18n from '@/i18n.js';
import GameModeMixin from '@/mixins/GameModeMixin.js'

export default {
mixins: [GameModeMixin],
components: {
  productHead, selectableProudct
},
methods: {
    ...mapMutations({
        setSelectedId: 'menus/setSelectedId',
        setData: 'pictureFirst/setData',
        setSelectedArticle: 'pictureFirst/setSelectedArticle',
        setRightArticle: 'pictureFirst/setRightArticle',
        setWrongArticle: 'pictureFirst/setWrongArticle',
        setHidden: 'pictureFirst/setHidden',
        incrementScore: 'pictureFirst/incrementScore',
        resetScore: 'pictureFirst/resetScore',
        setApplyButtonState: 'pictureFirst/setApplyButtonState',
        setNextRoundButtonState: 'pictureFirst/setNextRoundButtonState',
        appeared: 'pictureFirst/appeared',
    }),
    
    
},
computed: {
    ...mapState({
      data: state => state.pictureFirst.data,
      selectedArticle: state => state.pictureFirst.selectedArticle,
      rightArticle: state => state.pictureFirst.rightArticle,
      wrongArticle: state => state.pictureFirst.wrongArticle,
      isHidden: state => state.pictureFirst.isHidden,
      currentScore: state => state.pictureFirst.currentScore,
      highScore: state => state.pictureFirst.highScore,
      isApplyButtonDisabled: state => state.pictureFirst.isApplyButtonDisabled,
      isNextRoundButtonDisabled: state => state.pictureFirst.isNextRoundButtonDisabled,
      isAppearedBefore: state => state.pictureFirst.isAppearedBefore,
    }),
    
},

mounted() {
    this.setSelectedId(2);
}

}
</script>

<style lang="scss" scoped>
@import "@/styles/GameMode/GameMode.scss";

#pictureFirstParent{
    display: flex;
    flex-direction: column;
    margin-left: 0px;
}

#productsParent {
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    display: flex;
}

#currentProductImage {
    width: 200px;
    height: 200px;
    border: 1px solid;
    border-radius: 10px;
}

.productElement {
    display: flex;
    flex-direction: column;
    width: 375px;
    height: 64px;
}

#productNameListParent {
    margin-left: 25px;
    display: flex;
    height: 202px;
    flex-direction: column;
    justify-content: space-between;
}

@media (max-width: 650px) { 
    #productsParent { 
        flex-direction: column;
    }

    #productNameListParent {
        margin-top: 10px;
        margin-left: auto;
        height: auto;
    }

    #currentProductImageParent {
        display: flex;
    }
    #currentProductImage {
        width: 150px;
        height: 150px;
        margin: 0 auto;
        border: 1px solid;
        border-radius: 10px;
    }

    .productElement {
        margin-bottom: 10px;
    }

}

</style>