import Vue from 'vue'
import { createI18n } from 'vue-i18n'
import us from '@/locales/us.json'
import se from '@/locales/se.json'
import ru from '@/locales/ru.json'


export default createI18n({
    locale: 'us', 
    localeFlag: require(`@/images/flags/flagUS.png`),
    fallbackLocale: 'us',
    messages: {
        us: us,
        se: se,
        ru: ru
    }
})