<template>
  <header>
      <div class="IkeaMindLogoParent">
        <img class="IkeaMindLogo" src="@/images/IkeaMindNewLogo2NO_OUTLINE.png" />

        <div class="langSwitcher">
        <div @click="ShowPopup">
          <img class="flag pointer" :src="avaliableLangs[currentLocaleId].LangFlagName" />
        </div>
      </div>
      </div>
  </header>
</template>

<script>
import {mapState, mapMutations} from 'vuex'

export default {
    methods: {
    ...mapMutations({
            setSelectedId: 'menus/setSelectedId',
            ShowPopup: 'language/ShowPopup',
            SetCurrentLocaleId: 'language/SetCurrentLocaleId',
        }),
    },
    computed: { 
    ...mapState({
      menuItems: state => state.menus.menuItems,
      menuIcons: state => state.menus.menuIcons,
      selectedId: state => state.menus.selectedId,
      currentFlag: state => state.language.currentFlag,
      currentLocaleId: state => state.language.currentLocaleId,
      avaliableLangs: state => state.language.avaliableLangs,
    }),
},

}
</script>

<style lang="scss" scoped>
header {
    height: 65px;
    display: flex;
    justify-content: left; 
    margin: 0;
    padding: 0;
}

.IkeaMindLogoParent {
    margin: 0 29px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
    margin-top: auto;
    margin-bottom: auto;
}

.IkeaMindLogo {
    width: 203px;
    height: 35px;
    margin-top: auto;
    margin-bottom: auto;
}

@media (min-width: 801px) { 
    .langSwitcher {
        display: none;
    }
}

@media (max-width: 800px) { 
    header { 
        height: 55px;
    }
}

</style>