<template>
<div v-if="data != null" id="nameFirstParent">
    <product-head 
    :isDefaultGuy="(rightArticle != selectedArticle) ? isHidden : true" 
    :productName="data.products[data.rightProductID].productName"
    class="productHead"/>

    <div id="productListParent">
        <div id="productList" v-if="data != null">
            <selectable-proudct
            class="selectableProduct"
            v-for="product in data.products"
            :key="product.article"
            :isSelected="selectedArticle === product.article" 
            :productUrl="product.productURL"
            :productCategory="product.productCategory" 
            :productImageLink="product.imageURL" 
            :productName="product.productName"
            :isHidden="isHidden"
            :isRight="rightArticle === product.article"
            :isWrong="wrongArticle === product.article"
            @click="productItemClicked(product)"
            />
        </div>

    </div>

    <div id="WrongOrRight">
        <span :class="WrongOrRightStyle">
            {{WrongOrRightText}}
        </span>
    </div>


    <div id="buttons">
        <button 
        class="blueButton btn" 
        :disabled="isApplyButtonDisabled"
        @click="applyChoice"> 
            {{ $t('nameFirst.CheckIt')}}
        </button>

        <button 
        class="whiteButton btn" 
        :disabled='isNextRoundButtonDisabled'
        @click="nextRound"> 
            {{ $t('nameFirst.Next')}}
        </button>
    </div>

    <div id="statisticParent">
        <div>
            <span class="fontBlue">{{ $t('nameFirst.CurrentScore')}}</span>
            <span>{{currentScore}}</span>
        </div>
        <div>
            <span class="fontBlue">{{ $t('nameFirst.HighScore')}}</span>
            <span>{{highScore}}</span>
        </div>
    </div>



</div>  
</template>

<script>
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex'
import productHead from './components/Header.vue';
import selectableProudct from './components/SelectableProduct.vue';
import GameModeMixin from '@/mixins/GameModeMixin.js'

export default {
mixins: [GameModeMixin],
components: {
  productHead, selectableProudct
},
methods: {
    ...mapMutations({
        setSelectedId: 'menus/setSelectedId',
        setData: 'nameFirst/setData',
        setSelectedArticle: 'nameFirst/setSelectedArticle',
        setRightArticle: 'nameFirst/setRightArticle',
        setWrongArticle: 'nameFirst/setWrongArticle',
        setHidden: 'nameFirst/setHidden',
        incrementScore: 'nameFirst/incrementScore',
        resetScore: 'nameFirst/resetScore',
        setApplyButtonState: 'nameFirst/setApplyButtonState',
        setNextRoundButtonState: 'nameFirst/setNextRoundButtonState',
        appeared: 'nameFirst/appeared',
    }),
    

    
    
},
computed: {
    ...mapState({
      data: state => state.nameFirst.data,
      selectedArticle: state => state.nameFirst.selectedArticle,
      rightArticle: state => state.nameFirst.rightArticle,
      wrongArticle: state => state.nameFirst.wrongArticle,
      isHidden: state => state.nameFirst.isHidden,
      currentScore: state => state.nameFirst.currentScore,
      highScore: state => state.nameFirst.highScore,
      isApplyButtonDisabled: state => state.nameFirst.isApplyButtonDisabled,
      isNextRoundButtonDisabled: state => state.nameFirst.isNextRoundButtonDisabled,
      isAppearedBefore: state => state.nameFirst.isAppearedBefore,
    }),
  },

mounted() {
    this.setSelectedId(1);
}

}
</script>

<style lang="scss" scoped>
@import "@/styles/GameMode/GameMode.scss";

#nameFirstParent{
    display: flex;
    flex-direction: column;
    margin-left: 0px;
}

#productListParent {
    margin: 15px auto 0 auto;
}

#productList {
    display: flex;
    justify-content: space-between;
    width: 600px;
    max-width: 600px;
    flex-direction: row;
}

@media (max-width: 650px) { 
    #productListParent {
        display: flex;
        width: 100vw;
        margin-top: 10px;
    }
    #productList {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: auto;
        margin: 0 auto;
    }
    .selectableProduct {
        margin: 0 auto;
    }
}

</style>