<template>
  <div id="sideMenuParent">
      <div class="topMenus">
          <side-btn 
              v-for="item in menuItems"
              class="pointer"
              :key="item.Id"
              :id="item.Id" 
              @click="buttonClicked(item)"
              :iconUrl="menuIcons[item.Id].url"
              :isSelected="selectedId === item.Id"
              :popUpText="item.Title"/>
      </div>
      <div class="menuElement">
        <div @click="ShowPopup">
          <img class="flag pointer" :src="avaliableLangs[currentLocaleId].LangFlagName" />
        </div>
      </div>
  </div>
</template>

<script>
import sideBtn from './SideMenuButton.vue'
import router from '@/router'
import i18n from '@/i18n.js';
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex'

export default {
components: {
  sideBtn
},
methods: {
    ...mapMutations({
            setSelectedId: 'menus/setSelectedId',
            ShowPopup: 'language/ShowPopup',
            SetCurrentLocaleId: 'language/SetCurrentLocaleId',
        }),
    
    buttonClicked(item) {
            if(this.menuItems[item.Id].Link !== '' && item.Id != this.selectedId) {
                this.setSelectedId(item.Id)
                router.push({ name: this.menuItems[item.Id].LinkName, params: { lang: i18n.global.locale } });
            }
        },
},
computed: { 
    ...mapState({
      menuItems: state => state.menus.menuItems,
      menuIcons: state => state.menus.menuIcons,
      selectedId: state => state.menus.selectedId,
      currentFlag: state => state.language.currentFlag,
      currentLocaleId: state => state.language.currentLocaleId,
      avaliableLangs: state => state.language.avaliableLangs,
    }),
},

}
</script>

<style lang="scss">
#sideMenuParent {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.topMenus{
  margin-top: 74px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.pointer{
  cursor: pointer;
}

.menuElement {
  display: flex;
  justify-content: center;
  width: 40px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
}

.flag {
  width: 40px;
  height: 40px;
}

@media (max-width: 800px) {
  .topMenus {
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 55px;
    width: 100%;
  }

  .menuElement {
    display: flex;
    width: 35px;
    height: 35px;
    margin: auto 15px auto 0;
    display: none;
  }
}

</style>