<template>
<div id="productParent" :class="currentBackgroundName">
    <div id="productSecondParent">
        <img :class="currentStyleName" id="productPicture" :src="productImageLink" draggable="false" />
        <div class="spans">
            <span  id="productName">{{ isHidden ? "???" : productName}}</span>
            <span  id="productCategory">{{productCategory}}</span>
        </div>
    </div>
    <a :href="productUrl" v-if="!isHidden" :class="currentStyleName" class="showInIkeaParent">
            <div class="showInIkeaButton">
                <img src="@/images/icons/shoppingCartIcon.png" class="shoppingCartIcon" draggable="false" />
            </div>
    </a>
</div>
</template>

<script>
export default {
data() {
    return {
        currentStyleName: 'unselected',
        currentBackgroundName: 'unselectedBackground',
    }
},
methods: {
    handleCurrentStyle() {
        if(this.isRight && !this.isHidden) {
            this.currentStyleName = 'right'
            this.currentBackgroundName = 'rightBackground'
        }
        else if (this.isWrong) {
            this.currentStyleName = 'wrong'
            this.currentBackgroundName = 'wrongBackground'
        }
        else if(this.isSelected) {
            this.currentStyleName = 'selected'
            this.currentBackgroundName = 'selectedBackground'
        }
        else {
            this.currentStyleName = 'unselected'
            this.currentBackgroundName = 'unselectedBackground'
        }
    }
},

props: {
    productName: { String },
    productCategory: { String },
    productUrl: { String },
    productImageLink: { String },
    isSelected: { Boolean },
    isRight: { Boolean },
    isWrong: { Boolean },
    isHidden: { Boolean }
},

watch: {
    isSelected(){
      this.handleCurrentStyle();
    }, 
    isRight(){
      this.handleCurrentStyle();
    }, 
    isWrong(){
      this.handleCurrentStyle();
    }, 
    isHidden(){
      this.handleCurrentStyle();
    }
},

created() {
},

mounted() {
    this.handleCurrentStyle();
}

}
</script>

<style lang="scss" scoped>
@import "@/styles/GameMode/GameModeSelectableProduct.scss";

#productParent {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 10px;
    max-width: 181px;
}

#productSecondParent {
    display: flex;
    flex-direction: column;
    border: none;
}

#productPicture {
    width: 180px;
    height: 180px;
    border: 1px solid;
    border-radius: 10px;
}

.spans {
    display: flex;
    flex-direction: column;
}

#productName {
    font-family: 'Montserrat';
    font-weight: bold;
    font-size: 11pt;
    max-width: 180px;
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
}

#productCategory {
    margin-left: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
    font-family: 'Montserrat';
    font-size: 11pt;
    max-width: 180px;
}

.showInIkeaParent {
    display: flex;
 }

.showInIkeaButton {
    margin: auto 5px 5px auto;
}

@media (max-width: 650px) {  
    #productParent {
        max-width: 90vw;
        margin: 0 0 10px 0;
    }

    #productSecondParent {
        flex-direction: row;
        width: 90vw;
    }

    #productPicture{
        width: 100px;
        height: 100px;
    }

    .spans {
        margin-left: 15px;
    }

    #productName {
        width: auto;
    }

    #productCategory {
        width: auto;
    }
    
    .showInIkeaButton {
        margin-top: -35px;
    }
}

</style>