import axios from 'axios';
import i18n from '@/i18n.js';


export default {
    methods: {
        async fetchData() {
            var apiLink = 'https://ikeamind.itslk.space/Ikea/GetNextThreeProducts';
            if (window.location.protocol !== 'https:') 
                apiLink = 'http://ikeamind.itslk.space/Ikea/GetNextThreeProducts'

            const response = await axios.get(apiLink, {
                params: {
                    locale: i18n.global.locale
                }
            });
            function getRandomInt(max) {
                return Math.floor(Math.random() * max);
            }
            var randomRight = getRandomInt(3)
            response.data.rightProductID = randomRight;
            this.setRightArticle(response.data.products[randomRight].article);
            this.setData(response.data)
        },


        productItemClicked(product) {
            if(this.isHidden) {
                this.setSelectedArticle(product.article);
                this.setApplyButtonState(false);
            }
        },
        applyChoice() {
            if((this.selectedArticle != this.rightArticle)) {
                this.setWrongArticle(this.selectedArticle);
                this.resetScore();
            }
            else {
                this.incrementScore();
            }
    
            this.setHidden(false);
            this.setApplyButtonState(true);
            this.setNextRoundButtonState(false);
        },
        async nextRound() {
            this.setHidden(true);
            this.setSelectedArticle("0");
            this.setRightArticle("1");
            this.setWrongArticle("2");
            await this.fetchData();
            this.setApplyButtonState(true);
            this.setNextRoundButtonState(true);
        }
    },

    computed: {
        WrongOrRightText: {
            get: function() {
                return this.isHidden 
                ? "" 
                : (this.rightArticle != this.selectedArticle) ? i18n.global.t("nameFirst.Wrong") : i18n.global.t("nameFirst.Right");
            }
        }, 
        WrongOrRightStyle: {
            get: function() {
                return this.isHidden 
                ? "" 
                : (this.rightArticle != this.selectedArticle) ? "wrong" : "right";
            }
        }, 
    },

    created() {
        if (!this.isAppearedBefore) {
            this.fetchData();
            this.appeared();
        }
    },
}