<template>
<div id="prodcutElementParent" :class="currentBackgroundName">
    <div class ="productElement" >
        <span class="productElementName">{{productName}}</span>
        <span class="productElementCategory">{{ isHidden ? "???" : productCategory}}</span>
    </div>
    <a :href="productUrl" v-if="!isHidden" :class="currentStyleName" class="showInIkeaParent">
            <div class="showInIkeaButton">
                <img src="@/images/icons/shoppingCartIcon.png" class="shoppingCartIcon" draggable="false" />
            </div>
    </a>
</div>

</template>

<script>
export default {
data() {
    return {
        currentStyleName: 'unselected',
        currentBackgroundName: 'unselectedBackground',
    }
},
methods: {
    handleCurrentStyle() {
        if(this.isRight && !this.isHidden) {
            this.currentStyleName = 'right'
            this.currentBackgroundName = 'rightBackground'
        }
        else if (this.isWrong) {
            this.currentStyleName = 'wrong'
            this.currentBackgroundName = 'wrongBackground'
        }
        else if(this.isSelected) {
            this.currentStyleName = 'selected'
            this.currentBackgroundName = 'selectedBackground'
        }
        else {
            this.currentStyleName = 'unselected'
            this.currentBackgroundName = 'unselectedBackground'
        }
    }
},

props: {
    productName: { String },
    productCategory: { String },
    productUrl: { String },
    isSelected: { Boolean },
    isRight: { Boolean },
    isWrong: { Boolean },
    isHidden: { Boolean }
},

watch: {
    isSelected(){
      this.handleCurrentStyle();
    }, 
    isRight(){
      this.handleCurrentStyle();
    }, 
    isWrong(){
      this.handleCurrentStyle();
    }, 
    isHidden(){
      this.handleCurrentStyle();
    }
},

created() {
},

mounted() {
    this.handleCurrentStyle();
}
}
</script>

<style lang="scss" scoped>
@import "@/styles/GameMode/GameModeSelectableProduct.scss";

#prodcutElementParent{
    display: flex;
    flex-direction: row;
}

.productElementName {
    font-family: "Montserrat";
    font-weight: bold;
    font-size: 11pt;
    margin: 3px 10px 0 10px;
}

.productElementCategory {
    font-family: "Montserrat";
    font-size: 11pt;
    margin: 0 10px;
}

.productElement {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 64px;
    border-radius: 8px;
    
}

.showInIkeaParent {
    width: 45px;
    height: 64px;
    display: flex;
}

.showInIkeaButton {
    margin: auto;
}

@media (max-width: 650px) {  
    #prodcutElementParent {
        max-width: 90vw;
        width: 90vw;
    }
}

</style>