<template>
    <div id="langPopupParent">
        <div id="LangPopupHeaderParent">
            <div id="LangPopupHeaderText">
                <img class="SmallIcons" src="@/images/icons/SelectLangIcon.png" />
                <span id="SelectLangSpan">Select Language</span>
            </div>
            <div 
            @click="ClosePopup"
            id="LangPopupHeaderClose">
                <img id="closeIcon" class="SmallIcons" src="@/images/icons/close.svg" />
            </div>
        </div>
        <ul id="langElementParent">
            <li
            v-for="lang in avaliableLangs"
            :key="lang.Id"
            @click="changelanguage(lang)"
            id="langButtonParent">
                <img id="langButtonFlag" :src="lang.LangFlagName" />
                <span id="langButtonLangName">{{lang.LangName}}</span>
            </li>
        </ul>
        
    </div>
</template>

<script>
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex'
import router from '@/router'
import i18n from '@/i18n.js';
export default {
methods: {
    ...mapMutations({
        ClosePopup: 'language/ClosePopup',
        SetCurrentLocaleId: 'language/SetCurrentLocaleId',
    }),
    changelanguage(lang) {
        i18n.global.locale = lang.LangDomainName;
        router.push({ params: { lang: i18n.global.locale } });
        this.SetCurrentLocaleId(lang.Id)
        this.ClosePopup();
    }
},
computed: {
    ...mapState({
      avaliableLangs: state => state.language.avaliableLangs,
      isLangPopupVisible: state => state.language.isLangPopupVisible,
    })
}

}
</script>

<style lang="scss" scoped>
span {
    font-family: "Roboto";
}

#langButtonParent {
    display: flex;
    align-items: center;
    height: 88px;
    border: 1px #DFDFDF;
    border-style: solid none none none;
    padding-left: 25px;
}

#LangPopupHeaderParent{
    height: 70px;
    display: flex;
    align-items: center;
    padding: 0px 25px;
    font-size: 15pt;
    font-weight: bold;
    justify-content: space-between;
}

#LangPopupHeaderText {
    display: flex;
    align-items: center;
}

#LangPopupHeaderClose {
    display: flex;
    align-items: center;
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 20px;
    &:hover {
        background-color: #E8E8E8;
    }
}

.SmallIcons {
    width: 25px;
    height: 25px;
    margin: 0 auto;
}

#langElementParent{
    cursor: pointer;
    max-height: 330px;
    overflow: auto;
}

#SelectLangSpan{
    margin-left: 10px;
}

#langButtonFlag {
    width: 35px;
    height: 35px;
}

#langButtonLangName {
    font-size: 12pt;
    font-weight: bold;
    margin-left: 15px;
}

</style>