<template>
<div id="menuButtonParent"
  :title="$t(`sideMenuPrompts[${id}]`)">
    <img 
    id="Icon" 
    :src="iconUrl" 
    :class="currentStyleName"
    />
</div>
</template>

<script>
export default {
data() {
    return {
        currentStyleName: 'unselected'
    }
},
methods: {
handleCurrentStyle() {
    if(this.isSelected) {
        this.currentStyleName = 'selected'
    }
    else {
        this.currentStyleName = 'unselected'
    }
}
},
props: {
    id: { Number },
    iconUrl: { String },
    isSelected: { Boolean },
    popUpText: { String },
},

watch: {
    isSelected(){
      this.handleCurrentStyle();
    }
  },
  mounted() {
    this.handleCurrentStyle();
  }

}
</script>

<style lang="scss" scoped>
#menuButtonParent {
  width: 90px;
  height: 76px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#Icon{
  top: 50%;
  left: 50%;
}

.selected {
  filter: invert(16%) sepia(86%) saturate(3518%) hue-rotate(197deg) brightness(98%) contrast(101%);
  &::selection {
    filter:none;
  }
}
.unselected {
  filter: invert(92%) sepia(22%) saturate(4032%) hue-rotate(347deg) brightness(107%) contrast(97%);
  &::selection {
    filter:none;
  }
}


@media (max-width: 800px) { 
  #menuButtonParent {
    width: calc(100vw / 3);
    height: 35px;
    margin-top: auto;
    margin-bottom: auto;
  }

#Icon{
  width: 35px;
  height: 35x;
  }
  
}

</style>